<template>
  <div id="app">
    <home msg="Welcome to Your Vue.js App" />

    <div class="customStyle">
      <ul>
        <li>
            <img v-bind:width="svgWidth"  class="img-fluid" src="/web/langTWEN.svg" v-on:click="($i18n.locale=='tw')?$i18n.locale='en':$i18n.locale='tw'" style="cursor: pointer;" />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import home from "./home.vue";
// import buttom from './components/buttom.vue'
//  page, kind of food, dateTime, cart
export default {
  name: "app",
  data() {
    return {      
      svgWidth: 0,
    }
  },
  components: {
    home
  },
  methods: {
    onResize() {
      this.svgWidth = window.innerWidth/10;
      // console.log("app svg width->", this.svgWidth );
      if (this.svgWidth > 60){
        this.svgWidth=60;
      }
    }
  },

  created() {
    this.onResize();
    window.addEventListener("resize", this.onResize);
  },

  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  }
};
</script>

<style>
* {
  box-sizing: border-box;
}
</style>
