<template>
  <div v-if="vifTrue">
    <!-- swiper1 -->
    <swiper
      :options="swiperOptionTop"
      class="gallery-top"
      ref="swiperTop"
      :style="{'background-color': `rgb(245, 245, 245)`, 'color': `rgb(35, 35, 35)`,width:innerWidth+'px',height:innerHeight*0.92+'px'}"
      :key="1"
    >
      <swiper-slide :key="1">
        <div class="hidescroll" :style="styleSwiper">
          <aintroduction :appHeight="swiperHeight" :appWidth="swiperWidth" />
        </div>
      </swiper-slide>
      <swiper-slide :key="2">
        <div class="hidescroll" :style="styleSwiper">
          <bchart />
        </div>
      </swiper-slide>
      <swiper-slide :key="3">333</swiper-slide>
      <swiper-slide :key="4">444</swiper-slide>
      <swiper-slide :key="5">555</swiper-slide>
      <div class="swiper-button-next swiper-button-blue" slot="button-next" style="opacity: 0.1;"></div>
      <div class="swiper-button-prev swiper-button-blue" slot="button-prev" style="opacity: 0.1;"></div>
    </swiper>
    <!-- swiper2 Thumbs -->
    <swiper
      :options="swiperOptionThumbs"
      class="gallery-thumbs"
      ref="swiperThumbs"
      :style="{'background-color': `rgb(235, 235, 235)`, 'color': `rgb(35, 35, 35)`,width:innerWidth+'px',height:innerHeight*0.08+'px'}"
      :key="2"
    >
      <swiper-slide :key="1">
        <button
          type="button"
          class="btn btn-primary"
          :style="{ 'max-width':appWidth/4-10+'px'}"
        >Main</button>
      </swiper-slide>
      <swiper-slide :key="2">
        <button
          type="button"
          class="btn btn-secondary"
          :style="{ 'max-width':appWidth/4-10+'px'}"
        >Chart</button>
      </swiper-slide>
      <swiper-slide :key="3">
        <button
          type="button"
          class="btn btn-success"
          :style="{ 'max-width':appWidth/4-10+'px'}"
        >waiting</button>
      </swiper-slide>
      <swiper-slide :key="4">
        <button
          type="button"
          class="btn btn-danger"
          :style="{ 'max-width':appWidth/4-10+'px'}"
        >waiting</button>
      </swiper-slide>
      <swiper-slide :key="5">
        <button
          type="button"
          class="btn btn-warning"
          :style="{ 'max-width':appWidth/4-10+'px'}"
        >waiting</button>
      </swiper-slide>
    </swiper>
  </div>
</template>


<script>
//https://github.com/surmon-china/vue-awesome-swiper/blob/master/examples/31-thumbs-gallery-loop.vue
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

import aintroduction from "./resume/aintroduction";
import bchart from "./resume/bchart";
export default {
  data() {
    return {
      styleSwiper: {
        position: "absolute",
        top: "0px",
        left: "0px",
        width: "660px", //880px
        height: "660px", //750px
        "text-align": "left",
        "vertical-align": "top",
        "box-shadow": "0 2px 5px rgba(0,0,0,0.12), 0 2px 4px rgba(0,0,0,0.24)",
        "overflow-y": "scroll"
      },
      appWidth: 0,
      appHeight: 0,
      innerWidth: 0,
      innerHeight: 0,
      swiperWidth: 0,
      swiperHeight: 0,
      vifTrue: true,
      imageArrayWait: [],
      imageArray: [
        { photo: "/web/photo-camera-svgrepo-com.svg", path: "" },
        { photo: "/web/shopping-cart-svgrepo-com.svg", path: "" },
        { photo: "/web/folder-svgrepo-com.svg", path: "" },
        { photo: "date-svgrepo-com.svg", path: "/web/" },
        { photo: "/web/photo-camera-svgrepo-com.svg", path: "" },
        { photo: "/web/shopping-cart-svgrepo-com.svg", path: "" },
        { photo: "/web/folder-svgrepo-com.svg", path: "" },
        { photo: "date-svgrepo-com.svg", path: "/web/" },
        { photo: "/web/photo-camera-svgrepo-com.svg", path: "" },
        { photo: "/web/shopping-cart-svgrepo-com.svg", path: "" },
        { photo: "/web/folder-svgrepo-com.svg", path: "" },
        { photo: "date-svgrepo-com.svg", path: "/web/" }
      ],
      swiperOptionTop: {
        spaceBetween: 10,
        loop: true,
        loopedSlides: 5, //looped slides should be the same
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        slidesPerView: 1
      },
      swiperOptionThumbs: {
        spaceBetween: 10,
        slidesPerView: 10,
        touchRatio: 0.2,
        loop: true,
        loopedSlides: 5, //looped slides should be the same
        slideToClickedSlide: true,
        breakpoints: {
          1670: {
            slidesPerView: 8
          },
          1270: {
            slidesPerView: 6
          },
          850: {
            slidesPerView: 4
          }
        }
      }
    };
  },
  components: {
    swiper,
    swiperSlide,
    aintroduction,
    bchart
  },
  mounted() {
    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.swiper;
      const swiperThumbs = this.$refs.swiperThumbs.swiper;
      swiperTop.controller.control = swiperThumbs;
      swiperThumbs.controller.control = swiperTop;
    });
  },
  methods: {
    onReload() {
      if (typeof window.orientation !== "undefined" || navigator.userAgent.indexOf("IEMobile") !== -1) {
        // console.log("----Mobile");
        location.reload();
      } else {
        // console.log("----desktop");
        this.onResize();
      }
    },
    onResize() {
      // //console.log("window.nnerWidth->", window.innerWidth);
      // //console.log("window.innerHeight->", window.innerHeight);
      this.innerWidth = window.innerWidth;
      this.innerHeight = window.innerHeight;
      this.appWidth = window.innerWidth;
      this.appHeight = window.innerHeight;
      if (this.appWidth > 420) {
        this.appWidth = 420;
      }
      if (this.appHeight > 730) {
        this.appHeight = 730;
      }
      // //console.log("innerWidth->", this.appWidth);
      // //console.log("innerHeight->", this.appHeight);
      let space = 5;
      if (this.innerWidth >= 992) {
        let height = this.innerHeight * 0.92 - space * 2;
        this.styleSwiper.top = space + "px";
        this.swiperHeight = height;
        this.styleSwiper.height = height + "px";
        let width = (height / 3) * 4 - space * 2;
        if (width < 982) {
          width = 982;
          height = (width / 4) * 3;
          this.swiperHeight = height;
          this.styleSwiper.height = height + "px";
          let newTop = (this.innerHeight * 0.92 - height) / 2;
          if (newTop > 50) this.styleSwiper.top = newTop + "px";
        } else if (width > this.innerWidth - space * 2) {
          width = this.innerWidth - space * 2;

          height = (width / 4) * 3;
          this.swiperHeight = height;
          this.styleSwiper.height = height + "px";
          this.styleSwiper.top = (this.innerHeight * 0.92 - height) / 2 + "px";
        }
        this.swiperWidth = width;
        this.styleSwiper.width = width + "px";
        this.styleSwiper.left = (this.innerWidth - width) / 2 + "px";
      } else if (this.innerWidth >= 660) {
        // // this.innerWidth < 880
        // this.styleSwiper.top = space + "px";
        // this.swiperWidth = 982;
        // this.styleSwiper.width = "982px";
        // this.swiperHeight = (982 / 4) * 3;
        // this.styleSwiper.height = this.swiperHeight + "px";
        // this.styleSwiper.left = space + "px";

        let height = this.innerHeight * 0.92 - space * 2;
        this.styleSwiper.top = space + "px";
        this.swiperHeight = height;
        this.styleSwiper.height = height + "px";
        let width = (height / 3) * 4 - space * 2;
        if (width < 590) {
          width = 590;
          height = (width / 4) * 3;
          this.swiperHeight = height;
          this.styleSwiper.height = height + "px";
          let newTop = (this.innerHeight * 0.92 - height) / 2;
          if (newTop > 50) this.styleSwiper.top = newTop + "px";
        } else if (width > this.innerWidth - space * 2) {
          width = this.innerWidth - space * 2;

          height = (width / 4) * 3;
          this.swiperHeight = height;
          this.styleSwiper.height = height + "px";
          this.styleSwiper.top = (this.innerHeight * 0.92 - height) / 2 + "px";
        }
        this.swiperWidth = width;
        this.styleSwiper.width = width + "px";
        this.styleSwiper.left = (this.innerWidth - width) / 2 + "px";
      } else if (this.innerWidth >= 360) {
        let height = this.innerHeight * 0.92 - space * 2;
        this.styleSwiper.top = space + "px";
        this.swiperHeight = 0; //=height
        this.styleSwiper.height = height + "px";
        this.swiperWidth = this.innerWidth - space * 2;
        this.styleSwiper.width = this.swiperWidth + "px";
        this.styleSwiper.left = space + "px";
      } else {
        //< 360
        let height = this.innerHeight * 0.92 - space * 2;
        this.styleSwiper.top = space + "px";
        this.swiperHeight = 0; //=height
        this.styleSwiper.height = height + "px";
        this.swiperWidth = 360 - space * 2;
        this.styleSwiper.width = this.swiperWidth + "px";
        this.styleSwiper.left = space + "px";
      }
      //console.log("inner width->", this.innerWidth);
      //console.log("      height->", this.innerHeight);
      //console.log("swiper width->", this.swiperWidth);
      //console.log("       height->", this.swiperHeight);
      //console.log("style width->", this.styleSwiper.width);
      //console.log("      height->", this.styleSwiper.height);
    },
    doUpdateSync() {
      this.vifTrue = true;

      this.$nextTick(() => {
        const swiperTop = this.$refs.swiperTop.swiper;
        const swiperThumbs = this.$refs.swiperThumbs.swiper;
        swiperTop.controller.control = swiperThumbs;
        swiperThumbs.controller.control = swiperTop;
      });
    }
  },
  computed: {
    onsales() {
      return this.imageArray;
    }
  },
  created() {
    this.onResize();
    window.addEventListener("resize", this.onReload);
    window.addEventListener("orientationchange", this.onReload);
    // this.axios.post("http://192.168.1.66/getlatest").then(async response => {
    //   //  await this.$refs.mySwiper.swiper.removeAllSlides();
    //   //  await this.$refs.mySwiper.swiper.removeSlide(0);
    //   //  await this.$refs.mySwiper.swiper.removeSlide(0);
    //   //  await this.$refs.mySwiper.swiper.removeSlide(0);
    //   //  await this.$refs.mySwiper.swiper.removeSlide(0);
    //   this.vifTrue = false;
    //   this.imageArray = response.data;
    //   //console.log("----->", response);
    //   // //console.log("----->" , this.imageArray);

    //   setTimeout(this.doUpdateSync, 1000);
    //   // this.isSwiperDataUpdate=true;
    //   //  await    this.$refs.mySwiper.swiper.update();
    // });
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.onReload);
    window.removeEventListener("orientationchange", this.onReload);
  }
};
</script>
<style scoped>
/* http://beautifytools.com/scss-compiler.php */
.swiper-container {
  background-color: #000;
}

.gallery-top {
  height: 80%;
  width: 100%;
}

.gallery-thumbs {
  height: 20% !important;
  box-sizing: border-box;
  padding: 10px 0;
}

.gallery-thumbs .swiper-slide {
  width: 25%;
  height: 100%;
  opacity: 0.4;
}

.gallery-thumbs .swiper-slide-active {
  opacity: 1;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.hidescroll::-webkit-scrollbar {
  background-color: rgb(255, 255, 255);
  display: none;
}

/* Hide scrollbar for IE and Edge */
.hidescroll {
  background-color: rgb(255, 255, 255);
