import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import hami from '@/components/hami'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: HelloWorld,
      meta: { requiresAuth: true },
    },
    {
      path: '/hami',
      name: 'hami',
      component: hami,
      meta: { requiresAuth: true },
    },
    {
      path : '*',
      redirect : '/'
    }
  ]
})
