<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      svgWidth: 0
    };
  },
  methods: {
    onResize() {
      this.svgWidth = window.innerWidth / 10;
      // console.log("app svg width->", this.svgWidth);
      if (this.svgWidth > 60) {
        this.svgWidth = 60;
      }
    }
  },

  created() {
    this.onResize();
    window.addEventListener("resize", this.onResize);
  },

  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  }
};
</script>

