<template>
  <div>
    <div ref="busstop" style v-on:mousemove="showCoords($event)">
      <ve-line :data="chartData"></ve-line>
    </div>    
    <p><span style="color: green">Random Line</span> - Add a new random number per half second</p>
    <p><span style="color: blue">Mouse_Touch Line</span> - Set new values by moving the mouse or touching on the chart per half second, will add <span style="color: blue">{{userNumber}}</span></p>
  </div>
</template>

<script>
import VeLine from "v-charts/lib/line.common";
export default {
  components: { VeLine },
  data() {
    return {
      counter: 1,
      interval: null,
      chartData: {
        columns: ["times", "Random", "Mouse_Touch"],
        rows: []
      },
      chartY: 0,
      chartHeight: 0,
      isUser: false,
      userNumber: 5
    };
  },
  mounted() {
    let chartrect = this.$refs.busstop.getBoundingClientRect();
    this.chartY = chartrect.y;
    this.chartHeight = chartrect.height;
    // console.log("--->",this.chartY,this.chartHeight);
    this.chartData.rows = [];
    this.counter = 1;
    for (; this.counter < 31; this.counter++) {
      var newNumber = (Math.random() * 5).toFixed(0);
      this.chartData.rows.push({ times: this.counter.toString(), Random: newNumber, Mouse_Touch: 5 });
      this.counter++;
    }

    this.interval = setInterval(() => {
      // if (this.counter > 30) {
      // clearInterval(this.interval);
      // } else {
      // console.log("--> before", this.chartData.rows);
      this.chartData.rows.shift();
      var newNumber = (Math.random() * 5).toFixed(0);

      if (this.isUser) {
        this.isUser = false;
      } 
      
      this.chartData.rows.push({ times: this.counter.toString(), Random: newNumber, Mouse_Touch: this.userNumber });
      //

      this.counter++;

      // }
    }, 500);
  },
  methods: {
    showCoords(event) {
      //var x = event.clientX;
      var y = event.clientY;
      //var coords = "X coords: " + x + ", Y coords: " + y;
      //console.log("--->", coords, this.$refs.busstop.getBoundingClientRect());
      this.isUser = true;
      y = y - this.chartY;
      if (y < this.chartHeight * 0.2375) {
        this.userNumber = 5;
      } else if (y < this.chartHeight * 0.3625) {
        this.userNumber = 4;
      } else if (y < this.chartHeight * 0.4875) {
        this.userNumber = 3;
      } else if (y < this.chartHeight * 0.6125) {
        this.userNumber = 2;
      } else if (y < this.chartHeight * 0.7375) {
        this.userNumber = 1;
      } else {
        this.userNumber = 0;
      }
    }
  }
};
</script>

<style scoped>
* {      
    font-size: 25px;
}

@media screen and (max-width: 992px) {
* {
    font-size: 2.4vw;
  }
}
@media screen and (max-width: 660px) {
* {
    font-size: 4vw;
  }
}

@media screen and (max-width: 359px) {
* {
    font-size: 16px;
  }
}
</style>